/*
 *  Featured Collections
 *    Scripts for the featured collections section
 *
 *  Version:
 *    1.0.0 - 2021/09/21
 */

import { responsiveMediaBelow } from '../settings/responsive';
import { CLASS_ACTIVE, CLASS_INITIALISED, CLASS_VISIBLE } from '../tools/constants/classes';
import { fireInView } from '../tools/utilities/section-tools';

// Selectors
const SELECTOR_CONTAINER = '[data-featured-collections-container]';
const SELECTOR_LIST = '[data-featured-collections-list]';
const SELECTOR_LINK = '[data-featured-collections-link]';
const SELECTOR_IMAGE = '[data-featured-collections-image]';

// Attributes
const ATTR_COLLECTION_HANDLE = 'data-collection-handle';

// Set this up for every section on the page
document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(container => {
  if(container.classList.contains(CLASS_INITIALISED)) return;
  container.classList.add(CLASS_INITIALISED);

  const elementList = container.querySelector<HTMLElement>(SELECTOR_LIST);
  if(!elementList) return;

  // Setup event listeners for all the links
  elementList.querySelectorAll<HTMLElement>(SELECTOR_LINK).forEach(elementLink => {
    elementLink.addEventListener('mouseover', (e) => setCollectionImages({ e, container }));
  });

  // Mobile specific
  // Set the first link active by default by adding a class
  // Set the first collection (New Arrivals) pictures to show by default
  if (responsiveMediaBelow('MEDIUM')) {
    const firstLink = elementList.querySelector<HTMLElement>(SELECTOR_LINK);
    
    fireInView(elementList, () => {
      if(!firstLink) return;
      firstLink.classList.add(CLASS_ACTIVE);
    }, '0px', 1);

    // Remove the active class from the first link on touchstart event of other links
    elementList.querySelectorAll<HTMLElement>(SELECTOR_LINK).forEach(elementLink => {
      elementLink.addEventListener('touchstart', () => {
        if (elementLink !== firstLink) {
          firstLink?.classList.remove(CLASS_ACTIVE);
        };
      });
    });
  }
});

// The paramaters for the setCollectionImages function
type SetCollectionImagesParams = {
  e:Event;
  container: HTMLElement;
}

/**
 * Hides the unrelated images and shows the selected collection related images
 * @param params An object containing the needed event and container element
 * @returns void
 */
const setCollectionImages = (params:SetCollectionImagesParams) => {
  const { e, container } = params;

  const target = e.target as HTMLElement;
  if(!target) return;

  // This is for laptop and above only
  if(responsiveMediaBelow('MEDIUM')) return;

  // If the link is already active we don't need to do anything
  if(target.classList.contains(CLASS_ACTIVE)) return;

  // Match the collection handles
  const targetHandle = target.getAttribute(ATTR_COLLECTION_HANDLE);
  if(!targetHandle) return;

  // Find the matching images with that collection handle
  const matchingSelector = `${SELECTOR_IMAGE}[${ATTR_COLLECTION_HANDLE}="${targetHandle}"]`;
  const matchingImages = container.querySelectorAll<HTMLElement>(matchingSelector);
  if(!matchingImages || matchingImages.length < 1) return;

  // Remove other links active class
  container.querySelectorAll(`${SELECTOR_LINK}.${CLASS_ACTIVE}`).forEach(linkElement => {
    linkElement.classList.remove(CLASS_ACTIVE);
  });

  // Set target link to be active
  target.classList.add(CLASS_ACTIVE);

  // Remove visible class from current images
  const visibleSelector = `${SELECTOR_IMAGE}.${CLASS_VISIBLE}`;
  container.querySelectorAll<HTMLElement>(visibleSelector).forEach(imageElement => {
    imageElement.classList.remove(CLASS_VISIBLE);
  });

  // Add visible class to matching images
  matchingImages.forEach(imageElement => imageElement.classList.add(CLASS_VISIBLE));
}